.parent {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 30px;
}

/* https://cssgrid-generator.netlify.app/*/
.div1 { grid-area: 1 / 1 / 2 / 4; }
.div2 { grid-area: 1 / 4 / 2 / 9; }
.div3 { grid-area: 2 / 1 / 3 / 3; }
.div4 { grid-area: 2 / 3 / 3 / 9; }
.div5 { grid-area: 3 / 1 / 4 / 5; }
.div6 { grid-area: 3 / 5 / 4 / 9; }
.div7 { grid-area: 4 / 1 / 5 / 9; }
.div8 { grid-area: 5 / 1 / 6 / 5; }
.div9 { grid-area: 6 / 1 / 7 / 5; }
.div10 { grid-area: 5 / 5 / 7 / 9; }

.block {
  text-align: center;
  font-size: 1.2rem;
  max-height: 400px;
  background: #3d3d3d;
  opacity: 0;
  border: 3px solid black;
  border-radius: 2px;
  display: block;
  object-fit: cover;
}

.block.short {
  /*max-height: 340px;*/
}

.block.long {
  max-height: 836px;
}

.block img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

@keyframes flasher {
  0% {
    opacity: 0;
    transform: translateX(2000px);
  }
  60% {
    opacity: 1;
    transform: translateX(-30px);
  }
  80% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(0);
  }
}

.block.active {
  animation: flasher 0.5s;
  opacity: 1;
}
